.main-nav {
  margin-top: 35px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include vp-1023 {
      margin-top: 27px;
  }

  @include vp-767 {
    margin-top: 20px;

    .main-nav__list {
      display: none;
  }

      &--modal {
        flex-direction: column;
        margin-top: -2px;

          .main-nav__logo {
              margin-bottom: 35px;
              margin-right: 0;

              svg {
                  fill: $color-tintsoftangaroa;
              }
          }

          .main-nav__list {
              margin: 0 auto 0 0;
              display: flex;
              flex-direction: column;
              flex-wrap: nowrap;
          }

          .main-nav__item {
            padding: 10px;
              text-align: left;

              &:not(:last-child) {
                  margin-bottom: 6px;
              }
          }

          .main-nav__link {
              color: $color-tintsoftangaroa;
              letter-spacing: 0.2px;
              font-weight: 500;
          }
      }
  }

  &--nojs {

      @include vp-767 {
          margin-top: 20px;
          font-size: 18px;
          line-height: 22px;

          & ~ .page-header__title {
              margin-top: 40px;
          }

          .main-nav__list--nojs {
              display: flex;
              flex-direction: column;
              flex-wrap: nowrap;
          }
      }
  }

}

.main-nav__logo {
  width: 70px;
  height: 38px;
  margin-right: 40px;
  align-self: flex-start;

  @include vp-1023 {
      width: 38px;
      height: 20px;
  }

  @include vp-767 {
      align-self: flex-start;
      width: 42px;
      height: 22px;
  }

  svg {
      display: inline-block;
      vertical-align: middle;
      max-width: max-content;
      fill: $color-aliceblue;

      width: 70px;
      height: 38px;

      @include vp-1023 {
          width: 38px;
          height: 20px;
      }

      @include vp-767 {
          width: 42px;
          height: 22px;
      }
  }
}

.main-nav__toggle {
  display: none;

  @include vp-767 {
      display: block;
      position: relative;

      width: 24px;
      height: 16px;
      border: 0;
      background-color: $color-transparent;

      cursor: pointer;

      &::before {
          content: "";
          width: 24px;
          height: 2px;
          background-color: $color-default-white;
          position: absolute;
          top: 0;
          left: 0;
          box-shadow: 0 6px 0 0 $color-default-white, 0 12px 0 0 $color-default-white;
      }

      &--nojs {
          display: none;
      }
  }

}

.main-nav__list {
  margin: 0 0 0 auto;
  padding: 0;
  list-style: none;

  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.main-nav__item {
  text-align: right;


  &:not(:last-child) {
      margin-right: 33px;

      @include vp-1023 {
          margin-right: 26px;
      }

      @include vp-767 {
          margin-right: 0;
          margin-bottom: 10px;
      }
  }
}

.main-nav__link {
  font: 500 16px/19.2px $ff-montserrat;
  color: $color-aliceblue;

  @include vp-1023 {
      font: 14px/16.8px $ff-montserrat;
  }

  @include vp-767 {
      font: 18px/21.6px $ff-montserrat;
  }
}
